.contacts {
  text-align: center;
}

.contacts__map {
  position: relative;
  width: 100%;
  margin-bottom: 36px;

  @media (max-width: $desktop-width) {
    margin-bottom: 17px;
  }

  @media (max-width: $tablet-width) {
    margin-bottom: 26px;
  }
}

.contacts__map img {
  max-width: 100%;
  object-fit: cover;
  height: 530px;

  @media (max-width: $desktop-width) {
    height: 545px;
  }

  @media (max-width: $tablet-width) {
    height: 250px;
  }
}

.contacts__map iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 530px;

  @media (max-width: $desktop-width) {
    height: 545px;
  }

  @media (max-width: $tablet-width) {
    height: 250px;
  }
}

.contacts__list {
  @include flex;

  justify-content: center;
  padding: 0 10% 0 8%;

  @media (max-width: $tablet-width) {
    padding: 0;
  }
}

.contacts__item {
  margin-right: 90px;

  &:last-child {
    margin-right: 0;
  }

  @media (max-width: $tablet-width) {
    margin: 0;
    flex-basis: 100%;
  }

  @media (max-width: $tablet-width) {
    margin-bottom: 23px;
  }
}

.contacts__action {
  @include flex;

  align-items: center;
  font-style: normal;

  @media (max-width: $tablet-width) {
    justify-content: center;
  }
}

.contacts__list__icon {
  margin-right: 15px;
}
